.container {
    width: 1400px;
    position: relative;
    margin: 0 auto;
     }
  
  .signup-img, .signup-form {
    width: 50%; }
  
  .signup-img {
    margin-bottom: -7px; }
  
  .register-form {
    padding: 50px 100px 50px 70px; }
  
  .form-row {
    margin: 0 -15px; }
    .form-row .form-group {
      width: 50%;
      padding: 0 15px; }
  
  .form-group {
    margin-bottom: 23px;
    position: relative; }
  
  input, select {
    display: block;
    width: 100%;
    border: 1px solid #ebebeb;
    padding: 11px 20px;
    box-sizing: border-box;
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 13px; }
    input:focus, select:focus {
      border: 1px solid #ff6801; }
  
  label {
    font-size: 14px;
    font-weight: bold;
    font-family: 'Montserrat';
    margin-bottom: 2px;
    display: block; }
  
  .form-radio {
    margin-bottom: 18px; }
    .form-radio input {
      width: auto;
      display: inline-block; }
  
  .radio-label {
    padding-right: 72px; }
  
  .form-radio-item {
    position: relative;
    margin-right: 45px; }
    .form-radio-item label {
      font-weight: 500;
      font-size: 13px;
      padding-left: 25px;
      position: relative;
      z-index: 9;
      display: block;
      cursor: pointer; }
  
  .check {
    display: inline-block;
    position: absolute;
    border: 1px solid #ebebeb;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    -o-border-radius: 50%;
    -ms-border-radius: 50%;
    height: 13px;
    width: 13px;
    top: 4px;
    left: 0px;
    z-index: 5;
    transition: border .25s linear;
    -webkit-transition: border .25s linear; }
    .check:before {
      position: absolute;
      display: block;
      content: '';
      width: 9px;
      height: 9px;
      border-radius: 50%;
      -moz-border-radius: 50%;
      -webkit-border-radius: 50%;
      -o-border-radius: 50%;
      -ms-border-radius: 50%;
      top: 2px;
      left: 2px;
      margin: auto;
      transition: background 0.25s linear;
      -webkit-transition: background 0.25s linear; }
  
  input[type=radio] {
    position: absolute;
    visibility: hidden; }
    input[type=radio]:checked ~ .check {
      border: 1px solid #ff6801; }
    input[type=radio]:checked ~ .check::before {
      background: #ff6801; }
  
  .form-select {
    position: relative; }
  
  select {
    appearance: none !important;
    -moz-appearance: none !important;
    -webkit-appearance: none !important;
    -o-appearance: none !important;
    -ms-appearance: none !important;
    position: relative;
    background: 0 0;
    z-index: 10;
    cursor: pointer; }
  
  .select-icon {
    z-index: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    justify-content: center;
    -moz-justify-content: center;
    -webkit-justify-content: center;
    -o-justify-content: center;
    -ms-justify-content: center;
    align-items: center;
    -moz-align-items: center;
    -webkit-align-items: center;
    -o-align-items: center;
    -ms-align-items: center; }
    .select-icon i {
      justify-content: center;
      -moz-justify-content: center;
      -webkit-justify-content: center;
      -o-justify-content: center;
      -ms-justify-content: center;
      align-items: center;
      -moz-align-items: center;
      -webkit-align-items: center;
      -o-align-items: center;
      -ms-align-items: center;
      width: 40px;
      height: 20px;
      font-size: 18px;
      color: #999; }
  
  .form-submit {
    text-align: right;
    padding-top: 27px; }
  
  .submit {
    width: 140px;
    height: 40px;
    display: inline-block;
    font-family: 'Poppins';
    font-weight: 400;
    font-size: 13px;
    padding: 10px;
    border: none;
    cursor: pointer; }
  
  #reset {
    background: #f8f8f8;
    color: #999;
    margin-right: 8px; }
    #reset:hover {
      background: #ff6801;
      color: #fff; }
  
  #submit {
    background: #ff6801;
    color: #fff; }
    #submit:hover {
      background-color: #cd5300; }
  
  @media screen and (max-width: 992px) {
    .container {
      width: calc(100% - 40px);
      max-width: 100%; }
  
    .signup-content {
      flex-direction: column;
      -moz-flex-direction: column;
      -webkit-flex-direction: column;
      -o-flex-direction: column;
      -ms-flex-direction: column; }
  
    .signup-img, .signup-form {
      width: 100%; } }
  @media screen and (max-width: 768px) {
    .register-form {
      padding: 50px 40px 50px 40px; } }
  @media screen and (max-width: 575px) {
    .form-row {
      flex-direction: column;
      -moz-flex-direction: column;
      -webkit-flex-direction: column;
      -o-flex-direction: column;
      -ms-flex-direction: column;
      margin: 0px; }
  
    .form-row .form-group {
      width: 100%;
      padding: 0px; }
  
    .radio-label {
      padding-right: 22px; }
  
    .form-radio-item {
      margin-right: 25px; } }
  @media screen and (max-width: 480px) {
    .form-radio {
      flex-direction: column;
      -moz-flex-direction: column;
      -webkit-flex-direction: column;
      -o-flex-direction: column;
      -ms-flex-direction: column; }
  
    .submit {
      width: 100%; }
  
    #reset {
      margin-right: 0px;
      margin-bottom: 10px; } }